@import "https://fonts.googleapis.com/css2?family=Lexend:wght@100;300;400;500;600;700;800&display=swap";
@import "https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap";

$lexend: 'Lexend',
sans-serif;
$dancing: 'Dancing Script',
cursive;

$fa: #FAFAFA;
$fd: #FDFDFD;
$f2: #F2F2F2;
$cc: #CCCCCC;
$b: #000;
$w: #fff;
$cyan: #268A9B;
$cyan-7: rgba(38, 138, 155, 0.7);
$dark: #424242;

$linear: all linear .3s;

body {
  font-size: 14px;
  line-height: normal;
  font-family: $lexend;
}

a {
  text-decoration: none !important;
}

ul,
ol {
  padding: 0;
  list-style-type: none;
}

.relative {
  position: relative !important;
}

.lexend {
  font-family: $lexend !important;
}

.dancing {
  font-family: $dancing !important;
}

[class^="ri-"],
[class*=" ri-"] {
  vertical-align: bottom;
  font-size: 17px;
}


/*POOR EFFECTS*/
.shiny {
  position: relative;
  overflow: hidden;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    box-shadow: -5px 0 25px 5px $w;
    left: -10%;
    transition: $linear;
    transform: skewX(-10deg);
  }

  &:hover {
    &:after {
      left: 120%;
    }
  }
}

.scaleBig {
  transition: $linear;
  overflow: hidden;

  &:hover {
    transform: scale(1.1);
  }
}

.scaleSmall {
  transition: $linear;
  overflow: hidden;

  &:hover {
    transform: scale(0.9);
  }
}

.darken {
  position: relative;
  overflow: hidden;
  transition: $linear;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.6);
    transition: $linear;
    opacity: 0;
  }

  &:hover {
    &:after {
      visibility: visible;
      opacity: 1;
    }
  }
}

.lighten {
  position: relative;
  overflow: hidden;
  transition: $linear;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    background-color: rgba(255, 255, 255, 0.6);
    transition: $linear;
    opacity: 0;
  }

  &:hover {
    &:after {
      visibility: visible;
      opacity: 1;
    }
  }
}

/*POOR EFFECTS*/

header {
  box-shadow: 0 4px 9px rgba(0, 0, 0, 10%);

  .header {
    .searchbar {
      margin-right: auto;

      form {
        border-bottom: 1px solid;

        input {
          border: none;
          outline: none;
        }

        button {
          color: $b;
          background-color: transparent;
          border: none;
          font-size: 17px;
          outline: none;
        }
      }
    }

    .logo {
      font-size: 30px;

      img {
        height: 26px;
      }
    }

    .support-area {
      a {
        font-weight: 500;
      }

      .support {
        color: $cyan;
      }
    }

    .mainMenu {
      ul {
        li {
          a {
            position: relative;
            color: #000 !important;

            &::after {
              content: "";
              position: absolute;
              left: 2px;
              right: 100%;
              height: 7px;
              background-color: $cyan;
              opacity: .7;
              bottom: 8px;
              z-index: -1;
              transition: $linear;
            }

            &:hover {
              &::after {
                right: 0;
              }
            }
          }

          .active {
            &::after {
              right: 0;
            }
          }

          .disabled {
            color: rgba(0, 0, 0, .3) !important;
          }
        }

        .dropdown {
          a {
            &::after {
              display: none;
            }
          }

          .dropdown-menuu {
            position: absolute;
            top: 35px;
            z-index: 100;
            background-color: $w;
            border-radius: 5px;
            box-shadow: 0 0 3px rgba(0, 0, 0, 30%);
            min-width: 200px;
            padding: 10px;
            opacity: 0;
            visibility: hidden;
            transition: $linear;

            &::before {
              content: "";
              height: 30px;
              left: 0;
              right: 0;
              position: absolute;
              top: -10px;
              width: 100px;
            }
          }

          &:hover {
            .dropdown-menuu {
              opacity: 1;
              visibility: visible;
            }
          }
        }

        .mega-menu {
          a {
            &::after {
              display: none;
            }
          }

          .mega-menu-area {
            position: absolute;
            top: 84%;
            left: 0;
            display: flex;
            z-index: 100;
            background-color: $w;
            border-radius: 5px;
            box-shadow: 0 0 3px rgba(0, 0, 0, 30%);
            min-width: 200px;
            padding: 10px;
            opacity: 0;
            visibility: hidden;
            transition: $linear;

            .col {
              flex: 0 0 25%;
              max-width: 25%;
            }

            &::before {
              content: "";
              height: 20px;
              left: 0;
              right: 0;
              position: absolute;
              top: -8px;
            }
          }

          &:hover {
            .mega-menu-area {
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }
  }
}

.section-area {
  .section-title {
    h2 {
      text-align: center;
      font-size: 1.5rem;
    }
  }

  .section-description {
    text-align: center;
  }
}

.categories-slider {
  .slider-item .row img {
    border-radius: 10px;
  }
}

.categories-slider-nav {
  margin-top: 40px;

  .slick-list {
    .slick-track {
      .slick-slide {
        margin: 5px 0;

        >div {
          .categories-slider-thumb {
            width: 100px !important;
            height: 100px;
            background-size: cover;
            background-repeat: no-repeat;
            border-radius: 104px;
            opacity: 0.5;
            transition: $linear;
            transform: scale(0.9);
          }
        }
      }

      .slick-current {
        >div {
          .categories-slider-thumb {
            opacity: 1;
            transform: scale(1);
            box-shadow: 0 0 0 4px $cyan;
          }
        }
      }
    }
  }
}

.slick-slider {
  button::before {
    color: #000;
    content: "\f105";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }

  .slick-prev {
    &::before {
      content: "\f104";
    }
  }
}

.brands-slider {
  position: relative;


  .slick-list {
    .slick-track {
      .slick-slide {
        width: auto !important;
        margin: 0 10px;
      }
    }
  }

  a {
    img {
      border: 1px solid $fa;
      height: 60px;
      width: auto;
      border-radius: 10px;
      background-color: #fff;
    }
  }
}

.categories-area {
  .categories {
    position: relative;

    .categories-items {
      grid-template-columns: auto auto auto;
      grid-template-rows: auto auto auto auto auto auto;
      grid-gap: 10px;
      transition: $linear;

      >div {
        position: relative;
        border-radius: 5px;
        transition: $linear;

        a {
          padding: 20px;
          display: block;
          height: 100%;
          width: 100%;
          font-size: 20px;
          color: $b;
          transition: $linear;

          span {
            transition: $linear;
            display: block;
          }

          img {
            transition: $linear;
            height: 150px;
            width: auto;
            position: absolute;
            right: 0;
            bottom: 5%;
          }
        }
      }

      .one {
        grid-area: 1 / 1 / 7 / 2;
        background-color: #FFEDCD;
      }

      .two {
        grid-area: 1 / 2 / 5 / 3;
        background-color: #D8DBEB;
      }

      .three {
        grid-area: 5 / 2 / 7 / 3;
        background-color: #E2F0DE;
        height: 300px;
      }

      .four {
        grid-area: 1 / 3 / 2 / 4;
        background-color: #F9D9DA;
        height: 300px;
      }

      .five {
        grid-area: 2 / 3 / 7 / 4;
        background-color: #FEE3CE;
      }
    }
  }
}


.mix-banners-area {
  .mix-banners {
    .col {
      min-width: 20%;
    }
  }
}

.link-text-area {
  padding: 10px 20px;
  background-color: $cyan-7;
  font-family: $dancing;
  font-size: 20px;
  color: #fff;
  position: absolute;
  bottom: 5%;
  left: 0;
}

footer {
  background-color: $fa;

  .footer {
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 15px;
    padding: 60px 0 130px;

    .info {
      .logo {
        margin-bottom: 15px;

        img {
          height: 30px;
          width: auto;
        }
      }

      ul {
        li {
          display: flex;
          align-items: center;
          margin-bottom: 7px;

          i {
            vertical-align: middle;
            margin-right: 7px;
            font-size: 16px;
            color: $cyan;
          }

          a {
            display: flex;
            color: $b;
          }
        }
      }

      .social {
        a {
          display: inline-block;

          i {
            font-size: 23px;
            color: $cyan;
          }
        }
      }
    }

    ul {
      li {
        margin-bottom: 10px;

        a {
          color: $dark;
        }
      }
    }
  }
}

.product-card {
  text-align: center;
  padding: 0px;

  a {
    color: $dark;
    text-align: center;
    font-weight: 500;

    img {
      width: 100%;
    }

    .name {
      display: block;
      margin-top: 10px;
    }
  }
}

.grid-products-area {
  .grid-products {
    grid-template-columns: auto auto auto auto;
    grid-gap: 10px;

    .product-card {
      margin-bottom: 15px;
    }
  }
}

.slider-products-area {
  .slider-products {
    .product-card {
      padding-right: 5px;
      padding-left: 5px;
    }
  }
}

.breadcrumbs {
  border-bottom: 1px solid $f2;

  ol {
    display: flex;
    align-items: center;
    padding: 13px 0;

    li {

      a,
      i {
        color: $b;
      }

      &:last-child {
        a {
          color: $cyan;
          font-weight: 500;
        }

        i {
          display: none;
        }
      }
    }
  }
}

.category-left {
  ul {
    .active {
      color: $cyan;
      font-weight: 500;
    }

    a {
      color: $b;
      margin-bottom: 5px;
      display: block;
    }

    .sub-category {
      li {
        padding-left: 20px;
      }
    }
  }
}

.category-title {
  font-size: 17px;
  position: relative;
  display: inline-block;
  margin-bottom: 10px;
  padding-bottom: 5px;

  &::after {
    content: "";
    position: absolute;
    height: 2px;
    width: 70%;
    left: -5px;
    top: 100%;
    background-color: $b;
  }
}

.form-check-input:checked {
  background-color: $cyan;
  border-color: $cyan;
}

.form-check-input {
  box-shadow: none !important;
}

.color-input {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  input {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    -webkit-appearance: unset;
    transition: $linear;
    border: 3px solid #fff;
    border-radius: 50%;

    &:checked {
      box-shadow: 0 0 0 2px currentColor;
    }
  }
}

.color-input-area {
  display: flex;
  flex-wrap: wrap;

  .color-input {
    margin-right: 7px;
    margin-bottom: 7px;
  }
}

.category-right {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;

  .mobile-filter-btn-area {
    display: none;
  }

  .product-card {
    margin-bottom: 20px;
  }
}

.product-image-slider {
  img {
    border-radius: 10px;
  }
}

.product-image-slider-nav {
  img {
    border-radius: 10px;
    padding: 5px;
  }

  .slick-list {
    .slick-track {
      .slick-slide {
        opacity: 0.5;
      }

      .slick-current {
        opacity: 1;
      }
    }
  }
}

.product-right {
  a.cat {
    color: $cyan;
    font-weight: 600;
  }
}

.about_us-container {
  .about_us-icons {
    margin: 100px 0;
    display: flex;
    align-items: center;

    .icon {
      flex: 0 0 25%;
      max-width: 25%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;

      img {
        height: 70px;
        width: auto;
      }

      .icon-title {
        flex: 0 0 100%;
        display: block;
        margin-top: 10px;
        font-size: 16px;
        font-weight: 500;
      }
    }
  }
}

.float-input-box {
  position: relative;

  label {
    position: absolute;
    left: 10px;
    top: 20px;
    z-index: 5;
    transition: $linear;
    font-size: 14px;
  }

  input,
  textarea {
    position: relative;
    width: 100%;
    background-color: $fa;
    border: 1px solid $f2;
    border-radius: 5px;
    height: 60px;
    padding: 10px;
    padding-bottom: 0px;
    display: block;
    outline: none;

    &:focus,
    &:not(:placeholder-shown) {
      ~label {
        font-size: 12px;
        top: 5px;
        color: $cyan;
      }
    }
  }

  textarea {
    height: auto;
    padding-top: 20px;
  }
}

.contact-form {
  .contact-form-left {
    form {
      input[type="submit"] {
        padding: 20px 40px;
        background-color: $fa;
        border: 1px solid $f2;
        border-radius: 5px;
        font-weight: 600;
        color: $cyan;
      }
    }
  }
}


.theme-btn {
  padding: 20px 40px;
  background-color: $fa;
  border: 1px solid $f2;
  border-radius: 5px;
  font-weight: 600;
  color: $cyan;
}

.custom-pills {
  li {
    position: relative;

    button {
      background-color: transparent !important;
      padding: 0px 30px;
      color: $b !important;
      border-radius: 0px !important;
      padding-bottom: 5px;
      transition: $linear;
      border-bottom: 2px solid $f2 !important;
      width: 100%;
    }

    .active {
      border-color: $cyan !important;
    }
  }
}

.login-container,
.register-container {

  .login,
  .register {
    width: 30%;

    form {
      .theme-btn {
        padding: 15px;
        width: 100%;
      }
    }
  }
}

.theme-main-color {
  color: $cyan !important;
}

.navbar-toggler {
  margin-left: auto !important;
  box-shadow: none !important;
  border: none !important;
}

@media (max-width: 991px) {
  header {
    .header {
      .searchbar {
        margin-right: unset;
      }

      .support-area {
        display: none;
      }

      .mainMenu {
        ul {
          li {
            a {
              &::after {
                display: none;
              }
            }
          }

          .dropdown {
            .dropdown-menuu {
              position: relative;
              opacity: 1;
              visibility: visible;
              box-shadow: none;
              padding: 0 10px;
              top: unset;
            }
          }

          .mega-menu {
            .mega-menu-area {
              opacity: 1;
              position: relative;
              visibility: visible;
              box-shadow: none;
              padding: 0 10px;
            }
          }
        }
      }
    }
  }

  .support-area {
    display: none;
  }
}


@media (max-width: 768px) {
  header {
    .header {
      .searchbar {
        margin-right: unset;
      }

      .support-area {
        display: none;
      }

      .mainMenu {
        ul {
          li {
            a {
              &::after {
                display: none;
              }
            }
          }

          .dropdown {
            .dropdown-menuu {
              position: relative;
              opacity: 1;
              visibility: visible;
              box-shadow: none;
              padding: 0 10px;
              top: unset;
            }
          }

          .mega-menu {
            .mega-menu-area {
              opacity: 1;
              position: relative;
              visibility: visible;
              box-shadow: none;
              padding: 0 10px;
            }
          }
        }
      }
    }
  }

  .categories-slider-nav {
    width: 95%;
    margin: auto;

    .slick-list {
      .slick-track {
        display: flex;
        justify-content: center;
        padding-right: 130px;
        transition: $linear;

        .slick-slide {
          margin: 5px 50px;
        }
      }
    }
  }

  .categories-slider-nav .slick-list .slick-track .slick-slide>div .categories-slider-thumb {
    width: 75px !important;
    height: 75px !important;
  }

  .brands-slider {
    width: 95%;
    margin: auto;

    .slick-list {
      .slick-track {
        display: flex;
      }
    }
  }

  .mix-banners-area {
    .mix-banners {
      .col {
        min-width: 25%;
      }
    }
  }

  .link-text-area {
    font-size: 15px;
    right: 5px;
    letter-spacing: 1px;
  }

  .grid-products-area {
    .grid-products {
      grid-template-columns: auto auto auto;
    }
  }

  .slider-products-area {
    .slider-products {
      button {
        z-index: 10;

        &::before {
          padding: 7px 12px;
          background-color: #fff;
          border-radius: 50%;
        }
      }

      .slick-prev {
        left: 5px;
      }

      .slick-next {
        right: 15px;
      }
    }
  }

  footer .footer {
    grid-template-columns: auto auto;
    padding-right: 10px;
    padding-left: 10px;
    padding-bottom: 90px;

    >div,
    ul {
      margin-bottom: 10px;
    }
  }

  .category-left {
    display: none;
  }

  .category-right {
    margin: auto;

    .mobile-filter-btn-area {
      grid-area: 1/1/2/4;
      margin-bottom: 10px;

      display: grid;
      justify-content: right;

      .mobile-filter-btn {
        padding: 10px 25px;
        border: 1px solid #f2f2f2;
        background-color: $fa;
        border-radius: 5px;
      }
    }

    .mobile-filter-area {
      grid-area: 2/1/3/4;
      background-color: $fa;
      padding: 10px;
      border: 1px solid $f2;
      border-radius: 5px;
      margin-bottom: 15px;
    }
  }

  .breadcrumbs {
    padding-left: 30px;
  }

  .product-container {
    .product-right {
      padding-left: 0px !important;
    }
  }

  .login-container,
  .register-container {

    .login,
    .register {
      width: 50%;
    }
  }
}

@media (max-width: 576px) {
  header {
    nav {
      flex-direction: column-reverse;

      .searchbar {
        width: 95%;
        margin-top: 5px;

        form {
          input {
            width: 90%;
            padding-bottom: 10px;
          }
        }
      }

      .navbar-toggler {
        position: absolute;
        right: 0;
        top: 22%;
      }
    }
  }

  .categories-area {
    .categories {
      .categories-items {
        grid-template-rows: auto;
        grid-template-columns: auto;

        >div {
          height: 300px;
          position: relative;
          grid-area: auto !important;
        }
      }
    }
  }

  .mix-banners-area {
    .mix-banners {
      .col {
        min-width: 50%;
      }
    }
  }

  .grid-products-area {
    .grid-products {
      padding: 10px;
      grid-gap: 10px;
      grid-template-columns: auto auto;
    }
  }

  .category-right {
    grid-template-columns: auto auto;
    grid-gap: 10px;

    .mobile-filter-btn-area {
      grid-area: 1/1/2/3;
      padding: 0px;
      justify-content: unset;

      .mobile-filter-btn {
        width: 100%;
        text-align: center;
      }
    }

    .mobile-filter-area {
      grid-area: 2/1/3/3;
    }

    .product-card {
      padding: 0px;
    }
  }

  .custom-pills {
    display: block;
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;

    li {
      display: inline-block;
    }
  }

  .about_us-container {
    .about_us-icons {
      flex-wrap: wrap;
      margin: 50px 0;

      .icon {
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 50px;
      }
    }
  }

  .contact-form {
    .contact-form-right {
      iframe {
        width: 100%;
      }
    }
  }

  .login-container,
  .register-container {

    .login,
    .register {
      width: 100%;
    }
  }

  .product-image-slider {
    >button {
      z-index: 10;

      &::before {
        background-color: $w;
        border-radius: 50%;
        padding: 7px 12px;
      }
    }

    .slick-prev {
      left: 0;
    }

    .slick-next {
      right: 0;
    }
  }

  .brands-slider {
    width: 90%;
  }

  footer {
    .footer {
      grid-template-columns: auto;
    }
  }



}
